.tooltip2 {
  position: relative
}

.tooltip-item:after {
  content: "";
  position: absolute;
  width: 360px;
  height: 20px;
  bottom: 100%;
  left: 50%;
  pointer-events: none;
  transform: translateX(-50%)
}

.tooltip2:hover .tooltip-item:after {
  pointer-events: auto
}

.tooltip-content {
  padding: 10px 15px;
  position: absolute;
  z-index: 9999;
  width: 360px;
  left: 50%;
  margin: 0 0 10px -180px;
  bottom: 100%;
  text-align: left;
  box-shadow: 0 0 15px rgba(0,0,0,.53);
  border-radius: 3px;
  background: #2a3035;
  opacity: 0;
  cursor: default;
  pointer-events: none
}

.tooltip-content.position-down:after {
  top: -20px;
  border-bottom-color: #2a3035;
  border-top-color: transparent
}

.tooltip-content.position-right:after {
  bottom: auto;
  top: 50%;
  left: -10px;
  margin-top: -10px;
  border-right-color: #2a3035;
  border-top-color: transparent
}

.tooltip-content.position-left:after {
  bottom: auto;
  top: 50%;
  right: -20px;
  left: auto;
  margin-top: -10px;
  border-left-color: #2a3035;
  border-top-color: transparent
}

.tooltip-effect-1 .tooltip-content {
  transform: translate3d(0,-10px,0);
  transition: opacity .3s,transform .3s
}

.tooltip-effect-2 .tooltip-content {
  transform-origin: 50% calc(100% + 10px);
  transform: perspective(1000px) rotateX(45deg);
  transition: opacity .2s,transform .2s
}

.tooltip-effect-2 .tooltip-content.position-down {
  bottom: inherit;
  transform-origin: 50% calc(0% + 10px);
  margin-top: 0;
  margin-bottom: 0
}

.tooltip-effect-2 .tooltip-content.position-right {
  bottom: 50%;
  margin-left: 0;
  margin-bottom: 0;
  width: auto;
  left: 130%
}

.tooltip-effect-2 .tooltip-content.position-right .tooltip-text {
  white-space: nowrap
}

.tooltip-effect-2 .tooltip-content.position-left {
  bottom: 50%;
  margin-left: 0;
  margin-bottom: 0;
  width: auto;
  right: 130%;
  left: auto
}

.tooltip-effect-2 .tooltip-content.position-left .tooltip-text {
  white-space: nowrap
}

.tooltip-effect-2 .tooltip-content.tooltip-status-off {
  display: none!important
}

.tooltip-effect-3 .tooltip-content {
  transform: translate3d(0,10px,0) rotate3d(1,1,0,25deg);
  transition: opacity .3s,transform .3s
}

.tooltip-effect-4 .tooltip-content {
  transform-origin: 50% 100%;
  transform: scale3d(.7,.3,1);
  transition: opacity .2s,transform .2s
}

.tooltip-effect-5 .tooltip-content {
  width: 180px;
  margin-left: -90px;
  transform-origin: 50% calc(100% + 6em);
  transform: rotate(15deg);
  transition: opacity .2s,transform .2s;
  transition-timing-function: ease,cubic-bezier(.17,.67,.4,1.39)
}

.tooltip2:hover .tooltip-content {
  pointer-events: auto;
  opacity: 1;
  transform: translateZ(0) rotate3d(0,0,0,0)
}

.tooltip2.tooltip-effect-2:hover .tooltip-content {
  transform: perspective(1000px) rotateX(0deg)
}

.tooltip-content:after {
  content: "";
  top: 100%;
  left: 50%;
  border: solid transparent;
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-color: transparent;
  border-top-color: #2a3035;
  border-width: 10px;
  margin-left: -10px
}

.tooltip-content .img-wrap {
  margin-right: 15px
}

.tooltip-content .img-wrap img {
  display: block;
  max-width: 100%
}

.img-wrap,.tooltip-text {
  display: block;
