.checkbox {
  margin-bottom: 3rem
}

.checkbox label {
  position: relative;
  display: block;
  margin-bottom: .75rem;
  padding-left: 1.8rem;
  font-weight: 300;
  font-size: .75rem;
  line-height: 1.1rem;
  cursor: pointer;
  user-select: none
}

.checkbox a {
  display: inline-block;
  color: hsla(0,0%,100%,.6);
  border-bottom: 1px solid hsla(0,0%,100%,.6);
  text-decoration: none;
  transition: .5s
}

.checkbox a:hover {
  border-bottom: 1px solid hsla(0,0%,100%,0)
}

.checkbox .checkmark {
  position: absolute;
  left: 0;
  top: 2px;
  border: 2px solid #fff;
  border-radius: 4px;
  width: 1rem;
  height: 1rem;
  transition: .3s
}

.checkbox .checkmark:after {
  content: "";
  position: absolute;
  left: .275rem;
  top: 1px;
  display: block;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  width: .25rem;
  height: .5rem;
  opacity: 0;
  transform: rotate(45deg);
  transition: .3s
}

.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer
}

.checkbox input:checked~.checkmark:after {
  opacity: 1
}

