.modal {
  position: fixed;
  z-index: -9;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden
}

.modal,.modal__bg {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0
}

.modal__bg {
  position: absolute;
  z-index: 1;
  background-color: rgba(0,17,37,.8);
  backdrop-filter: blur(7px);
  transition: .5s
}

.modal__container {
  position: relative;
  z-index: 2;
  border-radius: .75rem;
  padding: 1.875rem 1.875rem 2rem;
  width: 60rem;
  max-width: 100%;
  background-color: #00162a;
  opacity: 0;
  transform: translateY(9.375rem);
  transition: .5s linear
}

@media screen and (max-width: 1000px) {
  .modal__container {
      margin-right:auto;
      margin-left: auto;
      width: calc(100vw - 1.875rem)
  }
}

@media screen and (max-width: 800px) {
  .modal__container {
      padding:2.5rem 2rem
  }
}

@media screen and (max-width: 500px) {
  .modal__container {
      padding:2rem 1rem
  }
}

.modal__footer,.modal__header {
  padding: 0;
  box-shadow: none;
  background-color: transparent
}

.modal__header {
  padding-top: 1rem;
  min-height: 3.5rem
}

.modal h2 {
  margin-bottom: 3rem;
  font-family: Montserrat;
  font-weight: 600;
  font-size: 2.353rem;
  letter-spacing: 2.1px
}

@media screen and (max-width: 1000px) {
  .modal h2 {
      font-size:2rem;
      line-height: 2.4rem
  }
}

@media screen and (max-width: 700px) {
  .modal h2 {
      font-size:1.7rem;
      line-height: 2rem
  }
}

@media screen and (max-width: 550px) {
  .modal h2 {
      margin-bottom:2rem;
      font-size: 1.3rem;
      line-height: 1.75rem
  }
}

@media screen and (max-width: 420px) {
  .modal h2 {
      margin-bottom:1.5rem;
      font-size: 1.1rem;
      line-height: 1.5rem
  }
}

.modal__cross {
  position: absolute;
  right: 2.1rem;
  top: 2rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 1rem;
  height: 1rem;
  transition: .5s ease-in;
  cursor: pointer
}

.modal__cross div {
  width: 18rem;
  height: .125rem;
  background-color: #3386bd;
  transform-origin: center
}

.modal__cross div:first-child {
  margin-top: .25rem;
  transform: rotate(45deg)
}

.modal__cross div:nth-child(2) {
  margin-top: -.75rem;
  transform: rotate(-45deg)
}

.modal__cross:hover {
  transform: rotate(180deg)
}

@media screen and (max-width: 800px) {
  .modal__cross {
      right:1.1rem;
      top: 1.5rem
  }
}

@media screen and (max-width: 550px) {
  .modal__cross {
      right:.7rem;
      top: .7rem
  }
}

@media screen and (max-width: 1000px) {
  .modal__wrap {
      overflow-y:auto
  }
}

.modal.open {
  z-index: 1004;
  opacity: 1;
  visibility: visible
}

.modal.open .modal__container {
  opacity: 1;
  transform: translateY(0)
}

.modal.open .modal__bg {
  opacity: 1
}

